.cart__header {
	display: flex;
	justify-content: space-between;
	width: 100%;
	align-items: center;
	margin-bottom: 30px;

	.buttons__right {
		display: flex;
		justify-content: center;
		align-items: center;

		button:nth-child(1) {
			margin-right: 30px;
		}
	}

	.button__common {
		border-radius: 20px;
		padding: 5px 15px;
		.MuiButton-label {
			display: flex;
			justify-content: center;
			align-items: center;

			p,
			a {
				font-size: 1rem;
				font-weight: 600;
				text-decoration: none;
				margin: 0 10px;
			}
			svg {
				transition: all 0.3s ease-in-out;
				transform: scale(1.5);
				margin-bottom: 3px;
			}
		}
	}

	.button__back {
		&:hover {
			background-color: transparent;

			.MuiButton-label {
				svg {
					transform: scale(1.5) translateX(-5px);
				}
			}
		}
		.MuiTouchRipple-root {
			color: rgba(63, 43, 59, 1);
		}
		.MuiButton-label {
			p {
				color: $primary-text-color;
			}
			svg {
				color: $primary-text-color;
			}
		}
	}

	.button__payment {
		background-color: $primary-main-color;
		&:hover {
			background-color: $primary-main-color;

			.MuiButton-label {
				svg {
					transform: scale(1.5) translateX(5px);
				}
			}
		}
		.MuiTouchRipple-root {
			color: rgb(83, 60, 185);
		}
		.MuiButton-label {
			p {
				color: $secondary-bg-color;
			}
			svg {
				color: $secondary-bg-color;
			}
		}
	}

	.button__whatsapp {
		background-color: #25d366;
		&:hover {
			background-color: #25d366;

			.MuiButton-label {
				svg:last-child {
					transform: scale(1.5) translateX(5px);
				}
			}
		}
		.MuiTouchRipple-root {
			color: rgb(64, 199, 114);
		}
		.MuiButton-label {
			a,
			p {
				color: $secondary-bg-color;
			}
			svg {
				color: $secondary-bg-color;
			}

			svg:first-child {
				margin-top: 2px;
			}
		}
	}

	.button__clear {
		&:hover {
			background-color: transparent;
			.MuiButton-label {
				svg {
					transform: scale(1.7);
				}
			}
		}
		.MuiTouchRipple-root {
			color: rgba(246, 68, 107, 1);
		}
		.MuiButton-label {
			p {
				color: $secondary-main-color;
			}
			svg {
				color: $secondary-main-color;
			}
		}
	}
}

@media (max-width: 720px) {
	.cart__header {
		// display: grid;
		// grid-template-columns: 1fr 1fr;
		// grid-template-rows: 50px 1fr;
		// width: 100%;
		// justify-content: flex-start;

		.button__back {
			margin-right: auto !important;
		}

		.buttons__right {
			display: grid;
			grid-template-columns: 1fr;
			justify-content: center;
			align-items: center;
			button:nth-child(1) {
				margin-right: 0px;
				margin-bottom: 30px;
			}
		}
	}
}

@media (max-width: 414px) {
	.cart__header {
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: 50px 1fr;
		width: 100%;
		justify-content: center;
		width: 90%;
		margin: 0 auto;

		.button__back {
			margin: auto !important;
		}

		.buttons__right {
			button:nth-child(1) {
				margin-right: 0px;
				margin-bottom: 10px;
			}
		}
	}
}
